.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  font-family: 'Gill Sans';
}
 
.topNavigation {
  background-color: #6DF7FF;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1.7rem;
  width: 100%;
}

.logo {
  width: 3rem;
  font-weight: bold;
}

.logo:hover {
  cursor: pointer;
}

.navLinks {
  display: flex;
  align-items: center;
  justify-content: center;

  /* .navLinksItem {
    cursor: pointer;
  } */
}

.navLinks a:after {
  /* // content: "‚óè"; */
  content: " ";
  font-size: 1.5rem;
  padding: 0 1rem;
  cursor: auto;
}

.navLinks a:last-child:after {
  content: none;
  padding: 0;
}

.hamburgerIcon {
  color: #000081;
  display: none;
  font-size: 1rem;
}

.navLinksMini {
  display: none;
}

@media (max-width: 860px) {
  .navLinks {
    display: none;
  }

  .navLinksMini {
    background-color: #6DF7FF;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 0em;
    margin: 0;
    left: 0;
    top: 1.3em;
    position: fixed;
    align-items: center;
    justify-content: center;
    visibility: hidden;
    transition: ease;
    transition-duration: 0.5s;
    overflow: hidden;

    /* a:
    div {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0.5em;
    } */
  }

  .open {
    height: 15em;
    visibility: visible;
    transition: ease;
    transition-duration: 0.5s;
  }

  .hamburgerIcon {
    display: block;
  }

  .hamburger {
    cursor: pointer;
    width: 3rem;
    margin: auto;
    transform: scale(2);
  }
}

